button{
    background-color: whitesmoke;
    border-style: ridge; 
    border-color: wheat;
    border-width: 1px;
    border-radius: 3px;
    height: 20px;
    transition-duration: 0.2s;
}

button:hover {
    background-color: dodgerblue;
    color: white;
}

.note{
    display: flex;
    gap: 10px;
    align-items: center;
}

.search{
    background-color:whitesmoke;
    align-items: center;
    padding-left: 10px;
    border-radius: 5px;
}

.error{
    border-radius: 10px;
    border-style: ridge;
    border-color: red;
    color: red;
    background-color: whitesmoke;
}
.error h3 {
    padding-left: 10px;
}